<template>
  <router-link :to="'/changeapplication/detail/' + changeApplictionItem.id" class="item" tag="div">
    <div class="row">
      <div>换班班次：</div>
      <div>{{changeApplictionItem.changeDate | moment('YYYY-MM-DD')}} {{changeApplictionItem.changeDON}} ({{changeApplictionItem.changeTime}})</div>
    </div>
    <div class="hr"></div>
    <div class="row">
      <div>个人班次：</div>
      <div>{{changeApplictionItem.originDate | moment('YYYY-MM-DD')}} {{changeApplictionItem.originDON}} ({{changeApplictionItem.originTime}})</div>
    </div>
    <div class="hr"></div>
    <div class="footer" :class="_statusColor(changeApplictionItem.status)">
      {{changeApplictionItem.status}}
    </div>
  </router-link>
</template>

<script>
export default {
  name: 'Item',
  props: {
    changeApplictionItem: {
      type: Object,
      default () {
        return {
          id: 1,
          changeDate: '2018-10-10',
          changeDON: '白',
          changeTime: '09:00-18:00',
          originDate: '2018-10-11',
          originDON: '夜',
          originTime: '18:00-06:00',
          status: '待确认'
        }
      }
    }
  },
  methods: {
    _statusColor (status) {
      switch (status) {
        case '待确认':
          return 'queren'
        case '待审批':
          return 'shenpi'
        case '已拒绝':
          return 'jujue'
        case '已驳回':
          return 'bohui'
        case '已通过':
          return 'tongguo'
        default:
          break
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../../styles/common';
.item{
  background: #fff;
  margin-top: @headerTop;
  overflow: hidden;
  padding-left: 16 * @px1;
  .row{
    height: 46 * @px1;
    line-height: 46 * @px1;
    display: flex;
    div:last-child{
      text-align: right;
      flex: 1;
      padding-right: 16 * @px1;
    }
  }
  .footer{
    text-align: right;
    padding-right: 16 * @px1;
    height: 44 * @px1;
    line-height: 44 * @px1;
    font-size: 14 * @px1;
  }
}
</style>
