<template>
  <div class="change_application page">
    <ul class="header">
      <li :class="{active: nowItem==='send'}" @click="toggleItem('send')">发起的换班</li>
      <li :class="{active: nowItem==='recieve'}" @click="toggleItem('recieve')">收到的换班</li>
    </ul>
    <div class="scroll_content_box">
      <item v-for="item in changeApplictions" :key="item.id" :changeApplictionItem="item"></item>
      <no-item v-if="changeApplictions.length===0"></no-item>
      <div id="blank" v-else></div>
    </div>
    <bottom-btn to="/changeapplication/add">新增换班</bottom-btn>
  </div>
</template>

<script>
import Item from './components/Item'
import NoItem from 'components/NoItem'

export default {
  name: 'ChangeApplication',
  components: {
    Item,
    NoItem
  },
  data () {
    return {
      nowItem: 'send',
      changeApplictions: [
        {
          id: 1,
          changeDate: '2018-10-10',
          changeDON: '白',
          changeTime: '09:00-18:00',
          originDate: '2018-10-11',
          originDON: '夜',
          originTime: '18:00-06:00',
          status: '待确认'
        },
        {
          id: 2,
          changeDate: '2018-10-10',
          changeDON: '白',
          changeTime: '09:00-18:00',
          originDate: '2018-10-11',
          originDON: '夜',
          originTime: '18:00-06:00',
          status: '待审批'
        },
        {
          id: 3,
          changeDate: '2018-10-10',
          changeDON: '白',
          changeTime: '09:00-18:00',
          originDate: '2018-10-11',
          originDON: '夜',
          originTime: '18:00-06:00',
          status: '已拒绝'
        },
        {
          id: 4,
          changeDate: '2018-10-10',
          changeDON: '白',
          changeTime: '09:00-18:00',
          originDate: '2018-10-11',
          originDON: '夜',
          originTime: '18:00-06:00',
          status: '已驳回'
        },
        {
          id: 5,
          changeDate: '2018-10-10',
          changeDON: '白',
          changeTime: '09:00-18:00',
          originDate: '2018-10-11',
          originDON: '夜',
          originTime: '18:00-06:00',
          status: '已通过'
        },
        {
          id: 6,
          changeDate: '2018-10-10',
          changeDON: '白',
          changeTime: '09:00-18:00',
          originDate: '2018-10-11',
          originDON: '夜',
          originTime: '18:00-06:00',
          status: '待确认'
        }
      ]
    }
  },
  methods: {
    toggleItem (item) {
      this.nowItem = item
    },
    getShiftsData () {
      this.$rpc.call('funenc_xa_station.change_shifts', 'get_change_shifts_list', [], {
        parameter: this.nowItem
      }).then(res => {
        this.changeApplictions = res
      }).catch(e => {
        console.log(e)
      })
    }
  },
  mounted () {
    this.getShiftsData()
  },
  watch: {
    nowItem () {
      this.getShiftsData()
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/common';
.change_application{
  .header{
    height: 46 * @px1;
    line-height: 46 * @px1;
    display: flex;
    padding-left: 16 * @px1;
    padding-right: 16 * @px1;
    text-align: center;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    li {
      flex: 1;
      font-weight: 500;
    }
    .active{
      color: @linkColor;
      position: relative;
      &:after{
        content: '';
        background: @linkColor;
        height: 2 * @px1;
        position: absolute;
        bottom: 2 * @px1;
        left: 54 * @px1;
        right: 54 * @px1;
      }
    }
  }
  .scroll_content_box{
    overflow-y: scroll;
    -webkit-overflow-scrolling : touch;
    height: 100%;
    box-sizing: border-box;
    padding-top: 46 * @px1;
  }
  #blank{
    height: 62 * @px1;
  }
}
</style>
